import { useState } from "react";
import { Divider, Tab, Header } from "semantic-ui-react";
import "./App.css";
import Generator from "./components/generator";
import { Genetic, GeneticParamsAnalyzer } from "./components/genetic";
import Greedy from "./components/greedy";
import { ReactComponent as Logo } from "./logo.svg";

const SandBox = () => {
  const [weights, setWeights] = useState([]);
  return (
    <>
      <br />
      <Generator onChange={setWeights} />
      <Divider horizontal />
      <Greedy weights={weights} />
      <Divider horizontal />
      <Genetic weights={weights} />
    </>
  );
};

const GeneticParamsDashboard = () => {
  const [weights, setWeights] = useState([]);
  return (
    <>
      <br />
      <Generator onChange={setWeights} />
      <Divider horizontal />
      <GeneticParamsAnalyzer weights={weights} />
    </>
  );
};

const Comparison = () => {
  const [weights, setWeights] = useState([]);
  return (
    <>
      <br />
      <Generator onChange={setWeights} />
      <Divider horizontal />
      <GeneticParamsAnalyzer weights={weights} />
    </>
  );
};

const panes = [
  { menuItem: "", render: () => <SandBox /> },
  { menuItem: "   ", render: () => <GeneticParamsDashboard /> },
  { menuItem: "    ", render: () => <Comparison /> },
];

function App() {
  return (
    <div className="App inverted">
      <h2 class="ui header">
        <Logo width="50" height="50" />
        <div class="content">   </div>
      </h2>
      <Tab panes={panes} />
    </div>
  );
}

export default App;
